<template>
    <!-- <h1 class="mb-20">{{ this.header }}</h1> -->
    <!-- Добавление автомобиля в каталог -->
    <!-- Будет присвоен ID: <b>{{ $store.state.updProd.id }}</b><br> -->
    <div v-if="$store.state.isProdsLoading">Идет загрузка...</div>
    <div v-else>
        <div v-if="!$store.state.updProd.id">
            ID: <b>{{ $route.params.id }}</b><br>
            <div v-html="insert.notFound"></div>
        </div>
        <div v-else>
            ID: <b>{{ $route.params.id }}</b>
            <form @submit.prevent class="mt-10">
                {{ insert.brandLabel }}:
                <div class="form-group mb-10">
                    <input
                        :class="['form-control w540', { notfilled: isFilled1 }]"
                        :placeholder="insert.brandPlaceholder"
                        :value="$store.state.updProd.brand"
                        @input="inputBrand"
                    />
                </div>
                {{ insert.modelLabel }}:
                <div class="form-group mb-10">
                    <input
                        :class="['form-control w540', { notfilled: isFilled2 }]"
                        :placeholder="insert.modelPlaceholder"
                        :value="$store.state.updProd.model"
                        @input="inputModel"
                    />
                </div>
                {{ insert.price }}:
                <div class="form-group mb-20">
                    <input
                        class="form-control w100 text-center inline-block" 
                        placeholder=""
                        :value="$store.state.updProd.price"
                        @input="inputPrice"
                    />
                </div>

                <multi-upload
                    ref="uplModule"
                    :uploadButton="insert.uploadButton"
                    :uploadedFiles="insert.uploadedFiles"
                />

                <hr class="w540 hr-dashed">
                <h5>{{ insert.translationHeader }}:</h5>
                {{ insert.brand2Label }}:
                <div class="form-group mb-10">
                    <input
                        :class="['form-control w540']"
                        v-model="$store.state.updProd.brand2"
                        :placeholder="insert.brand2Placeholder"
                    />
                </div>
                {{ insert.model2Label }}:
                <div class="form-group mb-10">
                    <input
                        :class="['form-control w540 mb-30']"
                        v-model="$store.state.updProd.model2"
                        :placeholder="insert.model2Placeholder"
                    />
                </div>
                <button @click="checkForm" class="btn2 greenBtn">{{ insert.updButton }}</button>
            </form>
        </div>
    </div>
</template>

<script>
import MultiUpload from '@/components/MultiUpload'
import { define } from '@/data/globals.js'
import useAuth from '@/hooks/useAuth'
import productsMixin from '@/mixins/productsMixin'
import axios from 'axios'
import useProducts from '@/hooks/useProducts'
import useNotify from '@/hooks/useNotify'
import notifys from '@/data/languages/notifications/FormEditData'
import useLang from '@/hooks/useLang'

export default {
    components: {
        MultiUpload,
    },
    mixins: [ productsMixin ],
    props: {
        insert: {
            type: Object,
            required: true
        },
    },
    data() {
        return {
            name: 'FormEdit',

            isFilled1: false,
            isFilled2: false,
        }
    },
    methods: {
        inputPrice(event) {
            // оставляем только цыфры и точку
            this.$store.commit('SetUpdPrice', this.checkPrice(event.target.value))
        },
        inputBrand(event) {
            this.$store.commit('SetUpdBrand', event)
            // сняте подсветки, если поле заполнено
            if(this.$store.state.updProd.brand.length !== 0) this.isFilled1 = false; else this.isFilled1 = true
        },
        inputModel(event) {
            this.$store.commit('SetUpdModel', event)
            // сняте подсветки, если поле заполнено
            if(this.$store.state.updProd.model.length !== 0) this.isFilled2 = false; else this.isFilled2 = true
        },
        
        // findUpdProd() {
        //     console.log('1 findUpdProd')
        //     if(this.$store.state.updProd.id > 0) return
        //     console.log('2 findUpdProd')

        //     console.log('3 this.$store.state.products: ', this.$store.state.products)

        //     // if(this.$store.state.products) {
        //     const { id, price, pictures, brand, model } = this.$store.state.products.find( item => item.id == this.$route.params.id )
        //     this.$store.commit('SetUpdProd', { id, price, pictures, brand, model })
        //     // this.$store.commit('SetUpdProd', { id, price, pictures, brand, model })
        //     // }
            
        //     this.getTranslations()
        // },

    // // Вернуть поля формы к значениям по умолчанию
    // resetUpdForm() {
    //     console.log('resetUpdForm!');
    //     const {id, name, description} = this.products.find(item => item.id === this.$store.state.updProd.id)
        
    //     this.$store.commit('setUpdProd', {id, name, description})
    //     // Возврат элементов из picsToDelete в стор pictures
    //     this.$refs.updForm.$refs.uplModule.getBackFiles()

    // },

        checkForm() {
            // подсветка пустых полей
            if(this.$store.state.updProd.brand.length === 0) this.isFilled1 = true
            if(this.$store.state.updProd.model.length === 0) this.isFilled2 = true
            // Поля заполнены - добавим товар
            if(!this.isFilled1 && !this.isFilled2) this.updateProduct()
        },



        async updateProduct() {
            console.log('\n\n@click ~~~~~~~~~~ updateProduct ~~~~~~~~~~ ')
            // console.log('Создание товара. Перекрыть экран ("заблокировать интерфейс") и показать спинер.\nОтправить запрос на бэк');
            
            const customAxios = axios.create({ // baseURL: 'http://yourcoolapi.com/api',
                withCredentials: true, 
            })

            customAxios.interceptors.request.use(
                config => {
                    console.log('>>>> customAxios - request (config)')

                    let localToken = localStorage.getItem('AccessToken')
                    if(localToken) localToken = encodeURI(localToken)
                    // Для информации:
                    // encodeURI заменяет киррилические и другие символы на соответствующими им UTF-8 escape-последовательности. Незаменяемые символы: латинский алфавит, десятичные цифры, - _ . ! ~ * ' ( ) #
                    config.headers.Authorization = 'Bearer ' + localToken
                    return config
                }
            )

            customAxios.interceptors.response.use(
                config => {
                    console.log('>>>> customAxios - response (config)')
                    return config;
                },
                async error => {
                    console.log('>>>> customAxios - response (error)')
                    const originalRequest = error.config
                    if (error.response.status == 401 &&
                        error.config &&
                        !error.config._isRetry
                    ) {
                        originalRequest._isRetry = true
                        console.log('> 401 !!!!!!!!!!!: ')
                        
                        if(await this.tokenRefresh()) {
                            console.log(' tokenRefresh TRUE!')
                            return customAxios.request(originalRequest)
                        }
                        // else {
                        //     console.log(' tokenRefresh FALSE! ')
                        //     console.log('НЕ АВТОРИЗОВАН')
                        // } // if

                    }
                    throw error; // прокидывает ошибки (как бы на верхний уровень), которые вернула функция tokenRefresh()
                } // async error
            ) // customAxios.interceptors.response

            // Изображения в сторе + новые
            const sumPics = [
                // ...this.$store.state.updProd.pictures,
                ...this.$store.getters.getPicsInDB,
                ...this.$refs.uplModule.newPics
            ]

            try {
                const response = await customAxios.post( define.pathToAPI + '/products/update.php',
                // const { data } = await customAxios.post( define.pathToAPI + '/products/create.php',
                // const response = await axios.post( define.pathToAPI + '/products/create.php',
                    JSON.stringify({
                        id: this.$store.state.updProd.id,
                        price: this.$store.state.updProd.price,
                        pictures: sumPics,
                        language: this.$store.state.language,
                        brand: this.$store.state.updProd.brand,
                        model: this.$store.state.updProd.model,

                        brand2: this.$store.state.updProd.brand2,
                        model2: this.$store.state.updProd.model2,
                    }),
                    {
                        // withCredentials: true, // определяет, должны ли создаваться кросс-доменные Access-Control запросы с использованием таких идентификационных данных как cookie, авторизационные заголовки или TLS сертификаты. Если здесь установили true, то на стороне бека нужно установить header("Access-Control-Allow-Credentials: true")
                        headers: {
                            // Authorization: 'Bearer YOUR_JWT_TOKEN_HERE',
                            Accept: 'application/json',
                            'Content-Type': 'application/json',
                        }
                    }
                )

                // console.log('response: ', response);
                // console.log('data: ', data);
                console.log('[3] response: ', response)
                // console.log('response.data: ', response.data);
                // console.log('response.data.status: ', response.data.status);

                // обработка ответа
                if(response.data.status === 'success') {

                    switch(response.data.localeStatus) {
                        case 'created':
                            // Изменения сохранены<br>Создана запись на втором языке
                            this.pushNotify( notifys[this.lang].text1, 'notifyGreen')
                            break
                        case 'success':
                            // Изменения сохранены
                            this.pushNotify( notifys[this.lang].saved, 'notifyGreen')
                            break
                        case 'nothingToChanges':
                            // Изменения сохранены
                            this.$store.commit('SetUpdProdTranslation', [ '', '' ] )
                            this.pushNotify( notifys[this.lang].saved, 'notifyGreen')
                            break
                        case 'deleted':
                            // Изменения сохранены<br>Запись на втором языке удалена
                            this.$store.commit('SetUpdProdTranslation', [ '', '' ] )
                            this.pushNotify( notifys[this.lang].text2, 'notifyGreen')
                            break
                        case 'failed':
                            // Изменения сохранены<br>Но возникла ошибка при внесении изменений на втором языке
                            this.pushNotify( notifys[this.lang].text3, 'notifyOrange')
                            break
                        default:
                            // Изменения сохранены<br>Но возникла непредвиденная ошибка (подробности в консоли)
                            this.pushNotify( notifys[this.lang].text4, 'notifyOrange')
                            console.log(response)
                            break
                    } // switch
                    // #####################################################################
                    // Сделано: удаление picsToDelete файлов ПОСЛЕ обновления товара в базе. 
                    // Протестить!
                    // #####################################################################
                    // Удаление picsToDelete файлов
                    this.$refs.uplModule.delPicsToDelete()

    // this.$emit('update')
    // // Обновление товара (в массиве)
    // updateData() {
                    // // Обновление полей объекта в массиве products
                    // this.products = this.products.map(item => item.id === this.$store.state.updProd.id ? {...this.$store.state.updProd} : item)
                    this.getProducts('updateProduct()')
                    // Очистка массива файлов, скрытие прогресс-бара и очистка поля input
                    this.$refs.uplModule.hideBarAndClean()
    // },

                    this.$store.commit('UpdatePicsInDB', sumPics)


                    // this.$store.commit('SetNewId', response.data.id)
                    // this.$store.commit('SetNewPictures', this.$refs.uplModule.newPics)
                    // // this.$store.commit('SetNewPictures', response.data.pictures)
                    // // this.$refs.uplModule.newPics
                    // // console.log('[Получили ответ "ОК, CREATED" -> \nФиксируем изменения во VUE/Пушим зеленое уведомление]')
                    // // this.createData()
                    // // добавление нового товара (в массив)
                    // this.$store.commit('AddProduct')
                    // this.$store.commit('ClearNewProd')
                    // // Очистка массива файлов, скрытие прогресс-бара и очистка поля input
                    // this.$refs.uplModule.hideBarAndClean()

                    // Отключение заморозки действий
                    this.$store.commit('SetFreeze', false)
                    // this.pushNotify('Изменения сохранены', 'notifyGreen')
                } else {
                    // Ошибка. Ответ сервера: ... <br>Подробности в консоли
                    this.pushNotify( notifys[this.lang].text5_1 + response.data.error + notifys[this.lang].text5_2, 'notifyOrange')
                    console.log('Ошибка:' + response)
                }

            } catch (e) {
                console.log('e: ', e);

                if(e.code === 'ERR_NETWORK') {
                    // Нет подключения к серверу
                    this.pushNotify( notifys[this.lang].errNetwork, 'notifyOrange')
                    console.log('Нет подключения к серверу')
                    return
                }

                // Обработка ошибок, которые вернула функция tokenRefresh()
                // (из customAxios.interceptors.response)
                // throw error прокидывает ошибки (как бы на верхний уровень)
                switch(e.response.data.status) {
                    case 'expired':
                        console.log(' expired [E1] (BOTH TOKENS IS EXPIRED) ');
                        // this.tokenRefresh()
                        break
                    case 'failed':
                        console.log(' failed [E2]');
                        // this.pushNotify('Alarm! Передан некорректный токен.<br>Доступ закрыт', 'notifyRed')
                        break
                    case 'denied':
                        console.log(' denied [E3]');
                        // this.pushNotify('Alarm! Передан пустой токен.<br>Доступ закрыт', 'notifyRed')
                        break
                    case 'approved':
                        console.log(' approved [E4] ');
                        // this.pushNotify('Alarm! Передан пустой токен.<br>Доступ закрыт', 'notifyRed')
                        break
                    case 'locked':
                        console.log(' locked [E5] ');
                        // this.pushNotify('Alarm! Передан пустой токен.<br>Доступ закрыт', 'notifyRed')
                        break
                    default:
                        // this.pushNotify('Ошибка. Подробности в консоли', 'notifyOrange')
                        // console.log(e)
                        console.log('error [E6]: ' + e)
                        break
                } // switch

            }
        },

    },// methods

    mounted() {
    // beforeMount() {
        this.getUpdProd(this.$route.params.id, true) // true - после получения updProd запустить getTranslations()
        // this.$store.commit('SetUpdId', +this.$route.params.id)
    },
    // mounted() {
    //     console.log('[FormEdit] -> mounted findUpdProd()')
    //     this.findUpdProd()
    // },
    watch: {
        '$store.state.language': function() {
            console.log('[' + this.name + '] -> language changes detected ')
            this.getUpdProd(this.$route.params.id, true) // true - после получения updProd запустить getTranslations()
        },
    },// watch
    // Composition API
    setup() {
        const { tokenRefresh } = useAuth()
        const { lang } = useLang('FormEdit')
        const { pushNotify } = useNotify()
        const { getProducts, getUpdProd, checkPrice } = useProducts()

        return { tokenRefresh, lang, pushNotify, getProducts, getUpdProd, checkPrice }
    }
}
</script>

